import { EditorView } from "@codemirror/basic-setup";
import { HighlightStyle, tags } from "@codemirror/highlight";

const chalky = "#e5c07b",
  coral = "#e06c75",
  cyan = "#56b6c2",
  invalid = "#ffffff",
  ivory = "#abb2bf",
  stone = "#7d8799", // Brightened compared to original to increase contrast
  malibu = "#61afef",
  sage = "#98c379",
  whiskey = "#d19a66",
  violet = "#c678dd",
  darkBackground = "#21252b",
  highlightBackground = "#2c313a",
  background = "#282c34",
  selection = "#3E4451",
  cursor = "#528bff";

export const lightTheme = EditorView.theme({
  "&": {
    fontSize: "12pt",
  },
  ".cm-content": {
    fontFamily: "Source Code Pro",
  },
  ".cm-lineWrapping": {
    wordBreak: "break-all",
  },
});

const darkColors = {
  background: "#242a36", // enki's dark.cardBackground
  primary: "#95FFCD", // dark.accent
  selection: "#547370", // "#3E4451" combined with the accent color #95FFCD twice
  highlightBackground: "#202530", // dark.codeBackground
  selectionMatch: "#aafe661a", // original theme
  text: "#f8f8f8", // enki text
  stone: "#7d8799", // from original theme
  textSecondary: "#f8f8f880",
  violet: "#c678dd",
  coral: "#e06c75",
  cyan: "#56b6c2",
  malibu: "#61afef",
  whiskey: "#d19a66",
  sage: "#98c379",
};

/// The editor theme styles for One Dark.
export const darkTheme = EditorView.theme(
  {
    "&": {
      color: ivory,
      backgroundColor: darkColors.background,
      fontSize: "12pt",
    },
    ".cm-lineWrapping": {
      wordBreak: "break-all",
    },
    ".cm-content": {
      caretColor: darkColors.primary,
      fontFamily: "Source Code Pro",
    },
    "&.cm-focused .cm-cursor": { borderLeftColor: darkColors.primary },
    "&.cm-focused .cm-selectionBackground, .cm-selectionBackground, .cm-content ::selection":
      { backgroundColor: darkColors.selection },

    // we don't use panels, but this is the working API
    // https://codemirror.net/6/examples/panel/
    // ".cm-panels": { backgroundColor: darkBackground, color: ivory },
    // ".cm-panels.cm-panels-top": { borderBottom: "2px solid black" },
    // ".cm-panels.cm-panels-bottom": { borderTop: "2px solid black" },

    // we don't use searching either
    // ".cm-searchMatch": {
    //   backgroundColor: "#72a1ff59",
    //   outline: "1px solid #457dff",
    // },
    // ".cm-searchMatch.cm-searchMatch-selected": {
    //   backgroundColor: "#6199ff2f",
    // },

    ".cm-activeLine": { backgroundColor: darkColors.highlightBackground },
    ".cm-selectionMatch": { backgroundColor: darkColors.selectionMatch },

    "&.cm-focused .cm-matchingBracket, &.cm-focused .cm-nonmatchingBracket": {
      backgroundColor: darkColors.selectionMatch,
      outline: `1px solid ${darkColors.primary}`,
    },

    ".cm-gutters": {
      backgroundColor: darkColors.background,
      color: darkColors.stone,
      border: "none",
    },

    ".cm-activeLineGutter": {
      backgroundColor: darkColors.highlightBackground,
    },

    // we don't use folding
    // ".cm-foldPlaceholder": {
    //   backgroundColor: "transparent",
    //   border: "none",
    //   color: "#ddd",
    // },

    // autocompletions
    ".cm-tooltip": {
      border: "1px solid #181a1f",
      backgroundColor: darkColors.background,
    },
    ".cm-tooltip.cm-tooltip-arrow:before": {
      borderTopColor: darkColors.background,
      borderBottomColor: darkColors.background,
    },
    ".cm-tooltip.cm-tooltip-arrow:after": {
      borderTopColor: darkColors.background,
      borderBottomColor: darkColors.background,
    },
    ".cm-tooltip-autocomplete": {
      "& > ul > li[aria-selected]": {
        // backgroundColor: darkColors.background,
        color: darkColors.text,
      },
    },
    ".cm-tooltip-autocomplete": {
      "& > ul > li": {
        color: darkColors.textSecondary,
      },
    },
  },
  { dark: true }
);

export const darkHighlighting = HighlightStyle.define([
  { tag: tags.keyword, color: darkColors.violet },
  { tag: tags.link, textDecoration: "underline", color: darkColors.stone }, // updated
  {
    tag: tags.heading,
    textDecoration: "underline",
    fontWeight: "bold",
    color: darkColors.coral,
  },
  { tag: tags.emphasis, fontStyle: "italic" },
  { tag: tags.strong, fontWeight: "bold" },
  { tag: tags.strikethrough, textDecoration: "line-through" },

  {
    tag: [
      tags.atom,
      tags.bool,
      tags.url,
      tags.contentSeparator,
      tags.labelName,
    ],
    color: darkColors.cyan,
  },
  { tag: [tags.deleted], color: darkColors.coral },
  {
    tag: [tags.processingInstruction, tags.string, tags.inserted],
    color: sage,
  },
  {
    tag: [tags.regexp, tags.escape, tags.special(tags.string)],
    color: darkColors.cyan,
  },
  { tag: tags.definition(tags.variableName), color: darkColors.whiskey },
  { tag: tags.local(tags.variableName), color: darkColors.whiskey },

  { tag: [tags.typeName, tags.namespace], color: chalky },
  { tag: tags.className, color: chalky },
  { tag: [tags.special(tags.variableName), tags.macroName], color: whiskey }, //what is this?
  { tag: tags.definition(tags.propertyName), color: darkColors.coral },
  { tag: tags.comment, color: stone },
  { tag: tags.meta, color: stone },
  { tag: tags.invalid, color: invalid },
  {
    tag: [tags.function(tags.variableName), tags.labelName, tags.propertyName],
    color: darkColors.malibu,
  },
  {
    tag: [tags.propertyName],
    color: darkColors.malibu,
  },
  // {
  //   tag: [tags.name], // this breaks android on mobile when typing x.forEach
  //   color: darkColors.whiskey,
  // },
  {
    tag: [tags.operator, tags.operatorKeyword],
    color: darkColors.sage,
  },
]);

export const lightHighlighting = HighlightStyle.define([
  { tag: tags.link, textDecoration: "underline" },
  { tag: tags.heading, textDecoration: "underline", fontWeight: "bold" },
  { tag: tags.emphasis, fontStyle: "italic" },
  { tag: tags.strong, fontWeight: "bold" },
  { tag: tags.strikethrough, textDecoration: "line-through" },
  { tag: tags.keyword, color: "#708" },
  {
    tag: [
      tags.atom,
      tags.bool,
      tags.url,
      tags.contentSeparator,
      tags.labelName,
    ],
    color: "#219",
  },
  { tag: [tags.literal, tags.inserted], color: "#164" },
  { tag: [tags.string, tags.deleted], color: "#a11" },
  { tag: [tags.regexp, tags.escape, tags.special(tags.string)], color: "#e40" },
  { tag: tags.definition(tags.variableName), color: "#00f" },
  { tag: tags.local(tags.variableName), color: "#30a" },
  { tag: [tags.typeName, tags.namespace], color: "#085" },
  { tag: tags.className, color: "#167" },
  { tag: [tags.special(tags.variableName), tags.macroName], color: "#256" },
  { tag: tags.definition(tags.propertyName), color: "#00c" },
  { tag: tags.comment, color: "#940" },
  { tag: tags.meta, color: "#7a757a" },
  { tag: tags.invalid, color: "#f00" },
]);

export const darkThemeExtensions = [darkTheme, darkHighlighting];

export const lightThemeExtensions = [lightTheme, lightHighlighting];
